import React from "react";
import Bit from "./Bit";

const BitList = (props) => {
  // retrieve bits from backend
  const bits = [
    {
      handle: "ben",
      display_name: "NoPressure1",
      content:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco l",
    },
    { handle: "sam", display_name: "GodFluxxy", content: "test bit" },
  ];

  return (
    <>
      {bits.map((bit, index) => (
        <Bit {...bit} key={index} index={index} />
      ))}
    </>
  );
};

export default BitList;
